@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.banner{
    display: flex;
    margin-top: 45px;
}
h1{
    font-weight: 900;
    font-size: 60px;
    color: white;
}
.banner-text{
    font-weight: normal;
    font-size: 20px;
    color: white;
    padding-bottom: 25px;
}
.banner-btn{
    width: 270px;
    height: 56px;
    border: none;
    background: linear-gradient(99.01deg, #FF9200 4.22%, #FFCC00 108.35%);
    border-radius: 50px;
}
.banner-btn:hover{
    box-shadow: 0px 4px 5px #FF9200, 0px 10px 15px #1678AC;
    transition: 0.9 ease;
}
.banner-btn a{
    font-weight: 600;
    font-size: 20px;
    color: white;
    text-decoration: none;  
}
.banner-bottom__text{
    padding-top: 150px;
    color: #333333;
    font-weight: normal;
    font-size: 17px;
    line-height: 180%;
}
.banner-right{
    position: absolute;
}
.banner-right img{
    width: 100%;
}
.banner-img{
    position: absolute;
    z-index: 2;
    top: 135px;
}
.header{
    background: linear-gradient(180deg, #00549A 0%, #20A0C9 81.85%);
    height: 700px;
}
.nav{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: #ffffff;
}
.header-second ul{
    display: flex;
    list-style-type: none;
}
.header-second ul li a{
    padding-left: 30px;
    color: white;
    text-decoration: none;
    font-size: 17px;
}
.header-third ul{
    display: flex;
    list-style-type: none;
}
.item-question{
    display: flex;
    align-items: center;
    padding-right: 30px;
    color: #FFCC00;
}
.item-question a{
    color: #FFCC00;
    font-size: 16px;
    font-weight: 600;
    padding-left: 20px;
}
.item-register{
    line-height: 8px;
}
.item-register p{
    text-align: end;
    font-size: 14px;
    font-weight: 600;
}
.item-register span{
    font-size: 20px;
    font-weight: 600;
}
.sec1{
    margin-top: 350px;
    color: #333333;
}
.sec1-title{
    display: flex;
    align-items: center;
    margin-bottom: 50px;
}
h3{
    color: #333333;
    font-size: 46px;
    padding-left: 25px;
    padding-right: 15px;
}
.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.card1{
    position: relative;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 20px;
    margin-bottom: 68px;
    width: 370px;
    height: 480px;
    background: #F2F7FA;
    border-radius: 6px;
}
.card1 img{
    margin-top: -38px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
h4{
    font-weight: 600;
    font-size: 22px;
}
.card1 span{
    font-weight: bold;
    font-size: 22px;
    line-height: 120%;
    color: #FF9200;
}
.card1 button{
    position: absolute;
    bottom: 30px;
    border: 2px solid #C1D0D9;
    box-sizing: border-box;
    border-radius: 50px;
    width: 187px;
    height: 56px;
}
.card1 button{
    text-decoration: none;
    color: #333333;
    font-size: 18px;
}
.card1 button:hover{
    background: linear-gradient(99.01deg, #FF9200 4.22%, #FFCC00 108.35%), #C4C4C4;
    border: none;
    color: #FFFFFF;
    transition: 0.9 ease;
}
.card1 a{
    color: #20A0C9;
    position: absolute;
    bottom: 48px;
    right: 70px;
}
.sec2{
    background: linear-gradient(180deg, #00549A 0%, #20A0C9 81.85%);
    height: 725px;
}
.sec2-top-title{
    padding-top: 40px;
    display: flex;
    align-items: center;
}
.sec2-top-title h3{
    color: white;
}
.sec2-top-title img{
    padding: 65px 0 0 0;
}
.sec2-top-text{
    display: flex;
}
.sec2-top-text li{
    display: flex;
    line-height: 25px;
    color: white;
    padding-right: 55px;
    list-style-type: none;
    padding-bottom: 40px;
    font-weight: normal;
    font-size: 16px;
}
.img{
    margin-left: -20px;
    margin-right: 15px;
}
.sec2-bottom{
    margin-top: 150px;
}
.sec2-bottom-title{
    display: flex;
    align-items: center;
}
.sec2-bottom-worker{
    display: flex;
    align-items: center;
}
.sec2-bottom-cards{
    display: flex;
    margin-top: 20px;
}
.sec2-bottom-card1{
    margin-right: 50px;
    text-align: center;
    width: 270px;
}
.sec2-bottom-card1 img{
    border-radius: 100px;
}
.sec2-bottom-card1 p{
    font-size: 16px;
    font-weight: 600;
}
.sec2-bottom-card1 span{
    padding-left: 2px;
    font-size: 15px;
    color: rgb(0, 139, 252);
}
.sec3{
    background: #F2F7FA;
    height: 904px;
    margin-top: 42rem;
}
.sec3-title{
    display: flex;
    align-items: center;
}
.sec3-title img{
    padding-top: 60px;
}
.sec3-text{
    font-size: 18px;
    color: #333333;
    line-height: 30px;
    margin-top: 10px;
    margin-bottom: 60px;
    padding-left: 25px;
}
.sec3-cards{
    display: flex;
    justify-content: space-around;
}
.sec3-card1{
    position: relative;
    padding-left: 20px;
    padding-right: 20px;
    width: 390px;
    height: 380px;
    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
}
.sec3-card1 img{
    position: absolute;
    right: 0;
}
.sec3-card1 h4{
    position: relative;
    font-weight: 600;
    font-size: 22px;
}
.sec3-card1 span{
    font-weight: bold;
    font-size: 22px;
    line-height: 120%;
    color: #FF9200;
}
.sec3-card1 button{
    position: absolute;
    bottom: 30px;
    border: none;
    background: linear-gradient(99.01deg, #FF9200 4.22%, #FFCC00 108.35%), #FF9200;
    border-radius: 50px;
    width: 341px;
    height: 56px;
}
.sec3-card1 button{
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    color: #FFFFFF;
}
.sec3-card1 button:hover{
    background: linear-gradient(0deg, #FFCC00, #FFCC00), #FF9200;
    color: #FFFFFF;
    transition: 0.9s ease;
}
.sec3-img{
    margin-left: 30px;
    margin-top: 70px;
}
.sec4{
    margin-top: 200px;
}
.sec4-title{
    display: flex;
    align-items: center;
}
.sec4-title img{
    padding-top: 60px;
    padding-right: 190px;
}
.sec4-text{
    margin-top: 15px;
    margin-left: 25px;
    font-size: 18px;
    line-height: 30px;
    color: #333333;
}
.sec4-cards{
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
}
.sec4-card1{
    text-align: center;
    width: 215px;
    height: 315px;
}
.sec4-img{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    width: 120px;
    height: 120px;
    background: linear-gradient(99.01deg, #FF9200 4.22%, #FFCC00 108.35%), #20A0C9;
    border-radius: 100px;
}
.sec4-card1 h4{
    font-weight: 600;
    font-size: 16px;
}
.sec4-card1 p{
    font-size: 14px;
    color: #333333;
}

.footer{
    margin-top: 60px;
    height: 882px;
    background: linear-gradient(180deg, #00549A 0%, #20A0C9 81.85%);
}
.footer-top{
    text-align: center;
    padding-top: 55px;
}
.footer-top h2{
    font-weight: bold;
    font-size: 52px;
    color: #FFFFFF;
    line-height: 5px;
}
.footer-top span{
    color: #FFCC00;
}
.footer-top p{
    font-weight: normal;
    font-size: 20px;
    color: #FFFFFF;
}
.register-form{
    display: flex;
    flex-direction: column;
}
.input-box{
    display: flex;
    justify-content: center;
    padding-top: 10px;
}
input{
    font-size: 16px;
    color: #ABB7C1;
    padding-left: 20px;
    width: 315px;
    height: 55px;
    border-radius: 50px;
    border: none;
}
.register-form button{
    margin-top: 20px;
    margin-left: 485px;
    border: none;
    background: linear-gradient(99.01deg, #FF9200 4.22%, #FFCC00 108.35%);
    box-shadow: 0px 4px 5px #FF9200, 0px 10px 15px #1678AC;
    border-radius: 50px;
    width: 341px;
    height: 56px;
}
.register-form button{
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    color: #FFFFFF;
}
.register-form button:hover{
    background: #FFCC00;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0px 4px 5px #FF9200, 0px 10px 15px #1678AC;;
    color: #FFFFFF;
    transition: 0.9 ease;
}
hr{
    margin-bottom: 40px;
    margin-top: 80px;
    border: 1px solid #33B1DD;
}
.footer-bottom li{
    font-size: 16px;
    line-height: 250%;
    color: #FFFFFF;
    list-style-type: none;
}
.footer-bottom li:hover{
    color: #FFCC00;
}
.footer-link-top{
    margin-left: -60px;
    display: flex;
    justify-content: space-around;
}
.footer-link-bottom{
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
    margin-left: 25px;
    line-height: 10px;
}
.footer-link-bottom li{
    line-height: 150%;
}
.footer-link-bottom ul{
    padding-right: 50px;
}
.footer-first p{
    color: #FFFFFF;
}
.footer-second{
    display: flex;
}
.footer-third li{
    line-height: 80%;
}
.footer-item-register p{
    font-weight: normal;
    font-size: 16px;
}
.footer-item-register span{
    font-size: 20px;
    font-weight: 600;
}
.footer-item-register2{
    padding-top: 15px;
    display: flex;
    align-items: center;
}
.footer-item-register2 img{
    padding-left: 15px;
}
body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}
.wrapper{
    max-width: 1400px;
    padding-right: 20px;
    padding-left: 20px;
    margin: 0 auto;
}

